<template>
  <div class="create-post">
    <Panel>
      <template #header>
        <router-link to="/">主页</router-link>
        <span class="gang">/</span>
        <span v-if="$route.path === '/post/create'">发布话题</span>
        <span v-else class="edit-topic">编辑话题</span>
      </template>
      <template #content>
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          class="demo-ruleForm"
        >
          <el-form-item prop="tab">
            <span>选择板块：</span>
            <el-select v-model="ruleForm.tab" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="title">
            <el-input
              v-model="ruleForm.title"
              placeholder="请输入内容"
            ></el-input>
          </el-form-item>
          <el-form-item prop="content">
            <mavon-editor
              :toolbars="markdownOption"
              v-model="ruleForm.content"
            />
          </el-form-item>
          <el-button type="primary" @click="submit">提交</el-button>
        </el-form>
      </template>
    </Panel>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import Panel from "../containers/Panel.vue";
import axios from "../plugins/axios";
import Turndown from "turndown";
export default {
  components: { Panel },
  data() {
    return {
      options: [
        {
          value: "ask",
          label: "问答",
        },
        {
          value: "share",
          label: "分享",
        },
        {
          value: "job",
          label: "招聘",
        },
        {
          value: "dev",
          label: "客户端测试",
        },
      ],
      markdownOption: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        strikethrough: true, // 中划线
        mark: true, // 标记
        quote: true, // 引用
        link: true, // 链接
        imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        fullscreen: true, // 全屏编辑
        readmodel: true, // 沉浸式阅读
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 1.3.5 */
        undo: true, // 上一步
        redo: true, // 下一步
        trash: true, // 清空
        save: true, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: true, // 导航目录
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true, // 预览
      },
      ruleForm: {
        title: "",
        tab: "",
        content: "",
      },
      rules: {
        tab: [{ required: true, message: "请选择类型", trigger: "change" }],
        title: [
          { required: true, message: "请输入标题", trigger: "blur" },
          { min: 10, message: "长度不能少于10个字符", trigger: "blur" },
        ],
        content: [{ required: true, message: "请输入标题", trigger: "blur" }],
      },
    };
  },
  computed: {
    ...mapState({
      post: (state) => state.showCurrentPost.post,
    }),
    id() {
      return this.$route.params.id;
    },
  },
  async created() {
    if (this.id) {
      await this.getPost(this.id);
      console.log(this.post);
      var turndownService = new Turndown();
      const { title, tab, content } = this.post;
      const newContent = turndownService.turndown(
        content.replace('<div class="markdown-text">', "").replace("</div>", "")
      );
      this.ruleForm = { title, tab, content: newContent };
    }
  },
  methods: {
    ...mapActions(["getPost"]),
    submit() {
      // 首先判断输入内容是否合理
      this.$refs.ruleForm.validate(async (boolean) => {
        if (boolean) {
            let { title, tab, content } = this.ruleForm;
            const accesstoken = localStorage.getItem("token");
            const res = await axios.post("/topics", {
              tab,
              title,
              content,
              accesstoken,
            });
            // console.log(this.$router);
            await this.$router.push(`/post/${res.topic_id}`);
        }
      });
    },
  },
};
</script>

<style>
.gang {
  margin: 0 10px;
}
.edit-topic{
  color: rgb(112, 112, 112);
}
</style>