// 未读消息数模块

import axios from '../../plugins/axios'
export default {
    state: () => ({
        message: 0
    }),
    mutations: {
        getMessageNum(state, message) {
            state.message = message
        }
    },
    actions: {
        async getMessageNum({ commit }, token) {
            // console.log('请求触发了')
            const res = await axios.get(`/message/count?accesstoken=${token}`)
            commit('getMessageNum',res.data)
        }
    }
}