<template>
  <Panel>
    <template #header>
      <router-link to="/">主页</router-link>
      <span>/</span>
      <span>登录</span>
    </template>
    <template #content>
      <el-form
        :model="ruleForm"
        :rules="rules"
        hide-required-asterisk
        ref="ruleForm"
        label-width="100px"
      >
        <el-form-item label="用户名" prop="username">
          <el-input
            placeholder="请输入用户名"
            style="width: 260px"
            autocomplete="off"
            v-model="ruleForm.username"
          ></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input
            placeholder="密码需要输入用户 token"
            style="width: 260px"
            autocomplete="off"
            v-model="ruleForm.password"
            type="password"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="login">登录</el-button>
        </el-form-item>
      </el-form>
    </template>
  </Panel>
</template>

<script>
import Panel from "../containers/Panel.vue";
import { mapActions } from "vuex";
export default {
  components: { Panel },
  data() {
    return {
      ruleForm: {
        username: "",
        password: "2ecdc1c3-bbac-445b-b5cf-8eede0be5995",
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
    };
  },
  //   computed: {
  //     ...mapState({
  //       info: (state) => state.login.info,
  //     }),
  //   },
  methods: {
    ...mapActions(["getInfo"]),
    login() {
      this.$refs.ruleForm.validate((boolean) => {
        if (boolean) {
          // 输入没有问题了
          this.getInfo(this.ruleForm.password)
            .then(() => {
              this.$router.push("/");
            })
            .catch(() => {
              this.$message.error("错了哦，token 有误");
            });
        }
      });
    },
  },
};
</script>

