<template>
  <div v-if="post">
    <Panel>
      <template #content>
        <div class="post-header">
          <h2>
            <span class="tag" v-if="post.top || post.good">{{
              post.top ? "置顶" : "精华"
            }}</span>
            {{ post.title }}
          </h2>
          <div>
            <p>
              <span class="disc">·</span>
              <span>发布于{{ post.create_at | moment("from", "now") }}</span>
              <span class="disc">·</span>
              <span>作者 {{ post.author.loginname }}</span>
              <span class="disc">·</span>
              <span>{{ post.visit_count }} 次浏览</span>
              <span class="disc">·</span>
              <span>来自 {{ post.tab | transformTab }}</span>
            </p>
            <el-button
              @click="collect"
              size="mini"
              :type="post.is_collect ? 'info' : 'success'"
              v-if="info"
              >{{ post.is_collect ? "取消收藏" : "收藏" }}</el-button
            >
          </div>
          <div v-if="info && post.author_id === '61653a14fe0c518938aee458'">
            <el-button
              type="primary"
              icon="el-icon-edit"
              circle
              size="mini"
              @click="edit"
            ></el-button>
          </div>
        </div>
        <div class="post-content" v-html="post.content">编辑</div>
      </template>
    </Panel>
    <Panel>
      <template #header>
        <span>{{ post.reply_count }} 回复</span>
      </template>
      <template v-if="post.replies.length" #content>
        <PostCommentItem
          v-for="(comment, index) in post.replies"
          :key="comment.id"
          v-bind="{ ...comment, index }"
        />
      </template>
    </Panel>

    <Panel v-if="info">
      <template #header>
        <span>添加回复</span>
      </template>
      <template #content>
        <mavon-editor v-model="content" />
        <el-button
          @click="publish"
          type="primary"
          style="margin-top: 20px; margin-left: 45%"
        >
          发布
        </el-button>
      </template>
    </Panel>
  </div>
  <el-skeleton v-else :rows="8" animated />
</template>

<script>
import { mapState, mapActions } from "vuex";
import Panel from "../containers/Panel.vue";
import PostCommentItem from "../components/PostCommentItem.vue";
export default {
  components: { Panel, PostCommentItem },
  data() {
    return {
      content: "",
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        strikethrough: true, // 中划线
        mark: true, // 标记
        superscript: true, // 上角标
        subscript: true, // 下角标
        quote: true, // 引用
        ol: true, // 有序列表
        ul: true, // 无序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        fullscreen: true, // 全屏编辑
        readmodel: true, // 沉浸式阅读
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 1.3.5 */
        undo: true, // 上一步
        redo: true, // 下一步
        trash: true, // 清空
        save: true, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: true, // 导航目录
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true, // 预览
      },
    };
  },
  computed: {
    ...mapState({
      post: (state) => state.showCurrentPost.post,
      info: (state) => state.login.info,
    }),
  },
  created() {
    this.getPost(this.$route.params.id);
    // console.log(this.info)
  },
  methods: {
    ...mapActions(["getPost", "collectHandle", "addComment"]),
    collect() {
      this.collectHandle({
        id: this.post.id,
        is_collect: !this.post.is_collect,
      });
    },
    publish() {
      if (this.content.trim()) {
        this.addComment({ id: this.post.id, content: this.content });
      }
    },
    edit() {
      this.$router.push(`/topic/${this.post.id}/edit`);
    },
  },
};
</script>

<style lang='less'>
.post-header {
  padding-bottom: 15px;
  border-bottom: 1px solid #ccc;
  > p {
    font-size: 13px;
    padding-bottom: 10px;
    border-bottom: 1px solid #ccc;
    color: #aaa;
    .cir {
      font-weight: 800;
      margin: 0 5px;
    }
  }
  .tag {
    font-size: 14px;
    padding: 2px 4px;
    border-radius: 3px;
    background-color: rgba(56, 194, 248, 0.918);
  }
  > div {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.post-content {
  img {
    width: 100%;
  }
}
</style>