<template>
  <div>
    <div v-if="list.length">
      <PostListItem v-for="post in list" :key="post.id" v-bind="post" />
      <div v-if="loading" class="loading">加载中...</div>
    </div>
    <el-skeleton v-else :rows="17" animated />
  </div>
</template>

<script>
import debounce from "lodash/debounce";
import { mapActions, mapState } from "vuex";
import PostListItem from "../components/PostListItem";
export default {
  components: { PostListItem },
  data() {
    return {
      page: 2,
      loading: false,
    };
  },
  async created() {
    const tab = this.$route.params.tab ? this.$route.params.tab : "all";
    await this.getPosts(tab);
    // console.log(this.list);
  },
  computed: {
    ...mapState({
      list: (state) => state.postList.list,
    }),
    tab() {
      return this.$route.params.tab || "all";
    },
  },
  watch: {
    "$route.params.tab": {
      async handler() {
        this.loading = true
        if(['all','ask','share','job','good','dev'].includes(this.tab)){
          await this.getPosts({ tab: this.tab });
          this.page = 2
        }else {
          this.$router.push({name: 'Err'})
        }
        this.loading = false
      },
      immediate: true,
    },
  },
  methods: {
    ...mapActions(["getPosts"]),
    scrollFun: debounce(async function () {
      // 获取到滚动条距离底部的距离
      // 滚动条距离顶部的距离 window.scrollY  document.documentElement.scrollTop
      // window 窗口的高度 window.innerHeight  document.documentElement.clientHeight
      // 文档的总高度 document.documentElement.scrollHeight
      const scrollHeight = document.documentElement.scrollHeight;
      const windowHeight =
        window.innerHeight || document.documentElement.clientHeight;
      const scrollTop = window.scrollY || document.documentElement.scrollTop;
      if (scrollHeight - windowHeight - scrollTop <= 200 && this.list.length) {
        // 因为进入页面的时候 当数据没来的时候，没有滚动条，默认可能会直接触发触底，但是并不想触发，判断就加了额外的条件
        if (!this.loading) {
          this.loading = true;
          this.page++;
          await this.getPosts({ tab: this.tab, page: this.page });
          this.loading = false;
        }
      }
    }, 300),
  },
  mounted() {
    window.addEventListener("scroll", this.scrollFun);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.scrollFun);
  },
};
</script>

<style>
.loading {
  text-align: center;
  line-height: 60px;
}
</style>