<template>
  <div v-if="user" class="user">
    <Panel>
      <template #header>
        <router-link to="/">主页</router-link>
      </template>
      <template #content>
        <div class="content">
          <el-avatar
            shape="square"
            size="medium"
            :src="user.avatar_url"
          ></el-avatar>
          <span>{{ user.loginname }}</span>
        </div>
        <div class="content">{{ user.score }} 积分</div>
        <div class="content">
          <i class="iconfont icon-github-fill"></i>
          <span
            ><a
              target="_blank"
              :href="`https://github.com/${user.githubUsername}`"
            >
              @{{ user.githubUsername }}</a
            ></span
          >
        </div>
        <div class="content create-time">
          <span>注册时间 {{ user.create_at | moment("from", "now") }}</span>
        </div>
      </template>
    </Panel>

    <Panel>
      <template #header> <span> 最近创建的话题 </span></template>
      <template #content>
        <PostListItem
          v-for="topic in recent_topics"
          :key="topic.id"
          v-bind="topic"
        />
      </template>
    </Panel>

    <Panel>
      <template #header> <span> 最近参与的话题 </span></template>
      <template #content>
        <PostListItem
          v-for="topic in recent_replies"
          :key="topic.id"
          v-bind="topic"
        />
      </template>
    </Panel>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import PostListItem from "../components/PostListItem.vue";
import Panel from "../containers/Panel.vue";
import axios from "../plugins/axios";
export default {
  components: { Panel, PostListItem },
  data() {
    return {
      recent_replies: [],
      recent_topics: [],
    };
  },
  watch: {
    "$route.params.username": {
      handler(newValue) {
        this.getUser(newValue);
      },
      immediate: true,
    },
    // 当 user 发生改变的时候，需要重新获取相关的 参与的话题和创建的话题详情
    user: {
      // 监听 user 的变换
      // 1. 首次进入 user 页，默认 user 是 null 然后 action 触发之后 user 更新了
      // 2. 当在当前页切换其他用户
      async handler(newValue) {
        const res1 = await axios.all(
          newValue.recent_replies.map((topic) =>
            axios.get(`/topic/${topic.id}`)
          )
        );
        this.recent_replies = res1.map((ele) => ele.data);
        console.log(res1)

        const res2 = await axios.all(
          newValue.recent_topics.map((topic) => axios.get(`/topic/${topic.id}`))
        );
        this.recent_topics = res2.map((ele) => ele.data);
      },
    },
  },
  computed: {
    ...mapState({
      user: (state) => state.user.user,
    }),
  },
  methods: {
    ...mapActions(["getUser"]),
  },
};
</script>

<style lang='less'>
.content {
  margin-bottom: 20px;
}
.create-time {
  span {
    color: rgb(155, 155, 155);
  }
}
</style>