import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import PostList from '../views/PostList.vue'
import Post from '../views/Post.vue'
import User from '../views/User.vue'
import Login from '../views/Login.vue'
import CreatePost from '../views/CreatePost.vue'
import Err from '../views/Err.vue'
import UnreadOrReadMessage from '../views/UnreadOrReadMessage.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    component: Login
  },
  {
    path: '/post/create',
    component: CreatePost
  },
  {
    path: '/topic/:id/edit',
    component: CreatePost
  },
  {
    path: '/err',
    name: 'err',
    component: Err
  },
  {
    path: '/post/:id',
    name: 'Post',
    component: Post
  },
  {
    path: '/user/:username',
    component: User
  },
  {
    path: '/message',
    name: 'message',
    component: UnreadOrReadMessage
  },
  {
    path: '/',
    name: 'Home',
    component: Home,
    children:[
      {
        // 默认子路由
        path:'',
        component:PostList
      },
      {
        path:':tab',
        component:PostList
      }
    ]
  },
  {
    path: '*',
    name: 'Err',
    component: Err
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
