<template>
  <div>
    <Panel>
      <template #header>
        <router-link to="/">主页</router-link>
        <span class="gang">/</span>
        <span class="new-message">新消息</span>
      </template>
      <template v-if="newMessage.length" #content>
        <PostListItem v-for="post in newMessage" :key="post.id" v-bind="post" />
      </template>
      <template #content v-else> <span>无消息</span></template>
    </Panel>
    <Panel>
      <template #header>
        <span>过往信息</span>
      </template>
      <template v-if="oldMessage.length" #content>
        <Reply v-for="list in oldMessage" :key="list.id" v-bind="list" />
      </template>
      <template #content v-else> <span>无消息</span></template>
    </Panel>
  </div>
</template>

<script>
import Panel from "../containers/Panel.vue";
import PostListItem from "../components/PostListItem.vue";
import Reply from "../components/Reply.vue";
import { mapActions, mapState } from "vuex";
export default {
  components: { Panel, PostListItem, Reply },
  computed: {
    ...mapState({
      newMessage: (state) => state.newOrOldMessage.newMessage,
      oldMessage: (state) => state.newOrOldMessage.oldMessage,
    }),
  },
  methods: {
    ...mapActions(["getNewMessage", "changeRead"]),
  },
  async created() {
    const token = localStorage.getItem("token");
    await this.getNewMessage(token);
    await this.changeRead();
  },
};
</script>

<style>
.gang {
  margin-left: 5px;
  margin-right: 5px;
}
.new-message {
  color: rgb(116, 115, 115);
}
</style>