<template>
  <div class="post-list-item">
    <router-link :to="`/user/${author.loginname}`">
      <el-avatar
        shape="square"
        size="medium"
        :src="author.avatar_url"
      ></el-avatar>
      <router-view></router-view>
    </router-link>
    <div v-if="reply_count" class="count-wrap">
      <span class="reply-count">{{ reply_count }}</span>
      <span>/</span>
      <span class="visit-count">{{ visit_count }}</span>
    </div>
    <span v-if="isHasTag" :class="['tag', { active: top || good }]">{{
      tagText
    }}</span>
    <router-link class="title" :to="`/post/${id}`"
      >{{ title }}
      <router-view></router-view>
    </router-link>
    <span class="last-reply-count">{{
      last_reply_at | moment("from", "now")
    }}</span>
  </div>
</template>

<script>
import Vue from "vue";
export default {
  props: [
    "author",
    "reply_count",
    "visit_count",
    "title",
    "last_reply_at",
    "top",
    "good",
    "tab",
    "id",
  ],
  computed: {
    isHasTag() {
      const page = this.$route.params.path;
      const isAllOrGoodPage =
        page === "/" || page === "/good" || page === "/all";
      return this.top ? true : isAllOrGoodPage ? true : false;
    },
    tagText() {
      return this.top
        ? "置顶"
        : this.good
        ? "精华"
        : Vue.filter("transformTab")(this.tab);
    },
  },
};
</script>

<style lang='less'>
.post-list-item {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #eee;
  .el-avatar {
    margin-right: 10px;
  }
  .count-wrap {
    width: 22px;
    margin-right: 75px;
    .reply-count,
    .visit-count {
      font-size: 14px;
    }
  }
  .tag {
    font-size: 12px;
    padding: 2px 4px;
    border-radius: 3px;
    background-color: #eee;
    // color: #fff;
    &.active {
      color: #fff;
      background-color: rgba(56, 194, 248, 0.918);
    }
  }
  .title {
    color: #000;
    margin: 14px 8px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    flex-grow: 1;
    cursor: pointer;
    &:visited {
      color: #ccc;
    }
  }
}
</style>