// 获取新的未读消息 或者 过往信息

import axios from '../../plugins/axios'
export default {
    state: () => ({
        newMessage: [],
        oldMessage: []
    }),
    mutations: {
        getNewMessage(state, message) {
            state.newMessage = message
        },
        getOldMessage(state, message) {
            state.oldMessage = message
        }
    },
    actions: {
        async getNewMessage({ commit }, token) {
            // console.log('请求触发了')
            const res = await axios.get(`/messages?accesstoken=${token}`, { mdrender: true })
            // console.log(res.data)
            commit('getNewMessage', res.data.hasnot_read_messages)
            commit('getOldMessage', res.data.has_read_messages)
        },
        async changeRead() {
            const token = window.localStorage.getItem("token");
            const res = await axios.post("/message/mark_all",{accesstoken:token});
        },
    }
}