<template>
  <div class="header">
    <div class="header-inner">
      <div class="header-left">
        <h1>
          <router-link to="/">
            <img
              src="https://img1.baidu.com/it/u=4220940872,2320089618&fm=26&fmt=auto"
              alt=""
            />
          </router-link>
        </h1>
        <el-input></el-input>
      </div>
      <div class="header-right">
        <router-link to="/">首页</router-link>
        <router-link v-if="!info" to="/login">登录</router-link>
        <div v-else>
          <!-- <el-badge :value="message" class="item" type="warning">
            <router-link to="/message"><span><span class="message-num"></span> 未读消息</span></router-link>
          </el-badge> -->
          <router-link to="/message">
            <span> 未读消息</span>
            <el-badge :value="message" class="item"> </el-badge>
          </router-link>
          <el-avatar
            class="avatar"
            size="medium"
            shape="square"
            :src="info.avatar_url"
          ></el-avatar>
          <span @click="loginOut" class="login-out">退出</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  computed: {
    ...mapState({
      info: (state) => state.login.info,
      message: (state) => state.messageNumber.message,
    }),
  },
  methods: {
    ...mapMutations(["getInfo"]),
    ...mapActions(["getMessageNum"]),
    loginOut() {
      if (this.$route.path !== "/") {
        this.$router.push("/");
      }
      localStorage.removeItem("info");
      localStorage.removeItem("token");
      this.getInfo(null);
    },
  },
  created() {
    const token = localStorage.getItem("token");
    this.getMessageNum(token);
  },
};
</script>

<style lang='less'>
.header {
  background: linear-gradient(to bottom, #000000 0%, #ffffff 100%);
  .header-inner {
    display: flex;
    justify-content: space-between;
    width: 83%;
    margin: 0 auto;
    .header-left {
      display: flex;
      .el-input__inner {
        width: 400px;
        margin-top: 24px;
        margin-left: 15px;
        box-shadow: 2px 2px 5px 0 #000000;
      }
      h1 {
        width: 130px;
        img {
          width: 100%;
          border-radius: 10px;
        }
      }
    }
    .header-right {
      display: flex;
      align-items: center;
      > div {
        display: flex;
        align-items: center;
        color: #fff;
        margin-left: 20px;
        font-size: 18px;
        span {
          // margin-right: 20px;
          cursor: pointer;
        }
      }
      a {
        color: rgba(248, 248, 248, 0.918);
        font-size: 18px;
        margin-left: 20px;
        line-height: 103px;
        margin-right: 20px;
      }
      .message-num {
        margin-right: 2px;
        font-size: 14px;
        color: #f83030d8;
      }
      .login-out {
        margin-left: 20px;
      }
    }
  }
}
</style>